.friendList {
  margin: 0 auto;
  width: 400px;
  list-style: none;
}
.item {
  display: flex;
  align-items: center;
  background-color: #fff;
  margin-bottom: 20px;
  padding: 10px 0 10px 10px;
  box-shadow: 3px 2px 3px 1px rgba(0, 0, 0, 0.089);
}
.status {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

.active {
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  margin-right: 10px;
  background-color: #00ff33;
}
.disabled {
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  margin-right: 10px;
  background-color: #ff0000;
}
.avatar {
  width: 48px;
  margin-right: 20px;
  border-radius: 8%;
}
.name {
  font-weight: 700;
  font-size: 24px;
}
