.profile {
  width: 300px;
  height: 400px;
  margin: 0 auto 40px;
  text-align: center;
  box-shadow: 3px 2px 3px 1px rgba(0, 0, 0, 0.089);
  border-radius: 3%;
  background-color: #fff;
  color: #122236;
  padding-top: 30px;
}
.description {
  color: #768696;
  font-weight: 300;
  font-size: 24px;
}
.avatar {
  border-radius: 50%;
  margin-bottom: 30px;
}
.name {
  color: #122236;
  font-weight: 700;
  margin-bottom: 10px;
}
.tag {
  margin-bottom: 10px;
  font-size: 16px;
}
.location {
  margin-bottom: 20px;
  font-size: 16px;
}
.stats {
  list-style: none;
  display: flex;
  justify-content: space-between;
  background-color: #f3f6f9;
}

.statsItem {
  border-top: 1px solid #e4e9f0;
  border-right: 1px solid #e4e9f0;
  width: 100px;
  padding: 33px 0 0 0;
}
.label {
  display: block;
  font-size: 16px;
}
.quantity {
  display: block;
  font-weight: 700;
  margin-bottom: 33px;
}
